@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Zilla Slab", serif;
}
.Contactus-body {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  background-image: url("https://imagesvc.meredithcorp.io/v3/jumpstartpure/image?url=https://cf-images.us-east-1.prod.boltdns.net/v1/static/507936866/4bf27859-6fb3-42ec-a9a8-4653ab788b45/50b11d00-44a6-4b04-8592-a758b053aa35/1920x1080/match/image.jpg&w=1280&h=720&q=90&c=cc");
  background-repeat: no-repeat;
  background-size: cover;
}

.container {
  padding: 20px;
  border: 1px solid lightgrey;
  background-color: rgba(255, 255, 255, 0.15);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  margin-top: 15px;
  border-radius: 8px;
  width: 50%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.container > h1 {
  margin: 10px 5px;
  color: white;
  text-align: center;
  text-shadow: 2px 2px rgb(156, 156, 156);
}
.inputcontainer {
  display: flex;
  flex-direction: column;
  margin: 20px 5px;
  font-size: 22px;
}
.inputcontainer > label {
  font-weight: 600;
  color: white;
}
.inputcontainer > label:hover {
  cursor: pointer;
}
.inputcontainer > input {
  font-size: 18px;
  padding: 4px 6px;
  color: white;
  background: transparent;
  border: none;
  border-bottom: 2px solid white;
  outline: none;
}
.inputcontainer > input::-webkit-input-placeholder {
  color: white;
}
.inputcontainer > input:-ms-input-placeholder {
  color: white;
}
.inputcontainer > input::placeholder {
  color: white;
}
.inputcontainer > textarea {
  font-size: 18px;
  padding: 4px 6px;
  max-width: 100%;
  color: white;
  background: transparent;
  border: none;
  border-bottom: 2px solid white;
  outline: none;
}
.inputcontainer > textarea::-webkit-input-placeholder {
  color: white;
}
.inputcontainer > textarea:-ms-input-placeholder {
  color: white;
}
.inputcontainer > textarea::placeholder {
  color: white;
}
.btn {
  cursor: pointer;
  margin: 10px 0px;
  display: block;
  margin: 5px auto;
  font-size: 22px;
  border-bottom: 5px solid #a763ff;
  border-top: none;
  border-left: none;
  border-right: none;
  padding: 2px 12px;
  background: linear-gradient(#3168ff, #9559b1);
  color: white;
  border-radius: 10px;
  box-shadow: 0px 2px 10px rgb(145, 165, 255);
  transition: 150ms ease;
  text-align: center;
  line-height: 50px;
  font-weight: 500;
}

.btn:active {
  border: none;
  border-bottom: 2px solid rgb(89, 114, 255);
  box-shadow: 0px 1px 5px grey;
  background: linear-gradient(#7192ff, #c695ff);
  color: #fff;
}
.errormessage {
  color: #ff3300;
}

.navbar-line {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 22px;
  background: linear-gradient(rgb(30, 30, 31), rgb(105, 105, 105));
  -webkit-backdrop-filter: blur(12px);
          backdrop-filter: blur(12px);
  z-index: 10;
  border-bottom: 1px solid black;
  border-top: 1px solid black;
}
.icon {
  width: 5vw;
}
.navbar-left {
  display: flex;
  align-items: center;
}
.navbar-left > h2 {
  color: rgb(255, 196, 87);
  padding: 0px 12px;
}
.navbar-option > ul {
  display: flex;
}
.nav-icon {
  margin: 0px 50px;
  font-size: 1.5rem;
  color: white;
  cursor: pointer;
  text-decoration: none;
}
.activeClass {
  text-decoration: underline !important;
  color: rgb(65, 227, 255) !important;
}
.nav-icon:hover {
  color: rgb(65, 227, 255);
}

.home-page {
  width: 100%;
  height: 100vh;
}
.home-body > img {
  width: 100%;
  height: 100vh;
  opacity: 0.89;
  overflow: hidden;
}
.button_container {
  position: absolute;
  left: 47%;
  top: 80%;
}

.dynamic-container {
  position: absolute;
  width: 50%;
  left: 29%;
  top: 55%;
}
.dynamic-text {
  color: #ffff00;
  font-size: 2rem;
  font-weight: 700;
  display: flex;
}
.button_container > a {
  position: relative;
  display: inline-block;
  padding: 5px 10px;
  color: #39ff14;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 600;
  overflow: hidden;
  transition: 0.2s;
}
.button_container > a:hover {
  color: #ffffff;
  background: #39ff14;
  box-shadow: 0 0 10px #39ff14, 0 0 40px #39ff14, 0 0 80px #39ff14;
  transition-delay: 1.1s;
}

.button_container > a > span {
  position: absolute;
  display: block;
}
.button_container > a > span:nth-child(1) {
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #39ff14);
}
.button_container > a:hover > span:nth-child(1) {
  left: 100%;
  transition: 1s;
}
.button_container > a > span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #39ff14);
}
.button_container > a:hover > span:nth-child(3) {
  right: 100%;
  transition: 1s;
  transition-delay: 0.5s;
}
.button_container > a > span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #39ff14);
}
.button_container > a:hover > span:nth-child(2) {
  top: 100%;
  transition: 1s;
  transition-delay: 0.25s;
}
.button_container > a > span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #39ff14);
}
.button_container > a:hover > span:nth-child(4) {
  bottom: 100%;
  transition: 1s;
  transition-delay: 0.75s;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto Slab", serif;
}

